import { Button, Col, Collapse, DatePicker, Divider, Empty, Form, message, Row, Select } from "antd";
import { CHILDREN, COMMON_MESSAGES, DATE_FORMAT, VALIDATION_FAILED } from "constants/CommonConstants";
import { STATUSES } from "constants/ManageAccounts";
import { EMPLOYEE_LABELS, EMP_FORM_FIELDS } from "constants/ManageEmployeeConstants";
import useIntlMessage from "hooks/useIntlMessage";
import moment from "moment";
import { default as React, useEffect, useState } from "react";
import employeeService from "services/EmployeeService";
import Utils from "utils";
import { DATE_FORMAT_YYYY_MM_DD } from "../../../../../constants/CommonConstants";

const EmployeeStatus = () => {
  const getMessage = useIntlMessage();
  const { Option } = Select;
  const [form] = Form.useForm();
  const [editEmployee, setEditEmployee] = useState(null);
  const [employeeList, setEmployeeList] = useState([]);
  const [loading, setLoading] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const [collapseState, setCollapseState] = useState(1);
  const [empStatus, setEmpStatus] = useState(editEmployee ? editEmployee.empStatus : null);
  const [empSubStatusStartDate, setEmpSubStatusStartDate] = useState(null);

  const { Panel } = Collapse;

  useEffect(() => {
    getEmployeeList();
  }, []);

  const formItemLayout = {
    labelCol: { span: 10 },
    wrapperCol: { span: 14 },
  };

  function disabledDate(current) {
    return current && current > moment().endOf("day");
  }

  const initialData = {
    empStatus: editEmployee?.empStatus,
    hasB1: editEmployee?.hasB1,

    subStatus: editEmployee?.empSubStatus || editEmployee?.subStatus,

    assignedLevel: editEmployee?.assignedLevel,
    // endDate: editEmployee?.endDate,
  };

  const employeeSubStatusList = ["At Client Location", "Onsite Travel", "Resigned"];

  const [employeeSubStatus, setEmployeSubStatus] = useState(
    editEmployee?.empSubStatus ? editEmployee?.empSubStatus : editEmployee?.subStatus ? editEmployee?.subStatus : null,
  );

  const isEmpSubStatusUpDated = () => {
    if (!Utils.isEmpty(editEmployee?.empSubStatus) || !Utils.isEmpty(editEmployee?.subStatus)) {
      return true;
    }
    return false;
  };

  function disabledBeforeDate(current) {
    const dateOfJoiningDate = moment(editEmployee?.dateOfJoining).format(DATE_FORMAT_YYYY_MM_DD);

    return current.isBefore(dateOfJoiningDate || moment().startOf("year"));
  }

  const disableBeforeDate = current => {
    return current && current.isBefore(empSubStatusStartDate || moment().startOf("month"));
  };

  // Memoized version of making fields disabled.
  const isEditMode = React.useCallback(() => {
    return editEmployee && true;
  }, [editEmployee]);

  const handleApiResponse = response => {
    if (response?.statusCode === 901 || response?.statusCode === 906 || response?.statusCode === 200) {
      message.success(getMessage(EMPLOYEE_LABELS.EMPLOYEE_UPDATE_SUCCESS));

      form.resetFields();
    } else {
      setErrorMessage(response?.message || "");
    }
  };

  const HandleSubmit = () => {
    form
      .validateFields()
      .then(async values => {
        try {
          const data = {
            employeeID: editEmployee?.employeeId,
            subStatus: editEmployee?.empSubStatus,
            toUpdateSubStatus: values?.empSubStatus,
            fromDate: values?.fromDate,
            toDate: values?.toDate,
            empExitDate: values?.endDate,
          };
          var response = await employeeService.updateEmployeeSubStatus(data);
          handleApiResponse(response);
        } catch (e) {
          console.error("Unable to save employee data", e);
        } finally {
          setLoading(false);
        }
      })
      .catch(info => {
        setCollapseState([1, 2, 3]);
        console.log(VALIDATION_FAILED.value, info);
      });
  };


  const getEmployeeList = async () => {
    try {
      setLoading(true);
      const resp = await employeeService.getAllEmployee();
      if (resp) setEmployeeList(resp);
    } catch (e) {
      console.error("Error in getting the employees information", e);
    } finally {
      setLoading(false);
    }
  };

  const getEmployee = async id => {
    try {
      setLoading(true);
      const resp = await employeeService.getSingleEmployeeById(id);
      if (resp) setEditEmployee(resp);
    } catch (e) {
      console.error("Error in getting the single employee information", e);
    } finally {
      setLoading(false);
    }
  };
  return (
    <>
      <Row align="middle" gutter={[20, 20]} className="mb-2" justify="end">
        <Col xs={24} md={12} lg={6} xl={6}>
          <Form name="basic" form={form}>
            <Form.Item
              name="manage-project-select-account"
              label={getMessage("common.label.select-employee")}
              rules={[{ required: true, message: "Select Employee" }]}
              className="mb-0"
            >
              <Select
                className="w-100"
                placeholder={getMessage("common.label.select-employee")}
                name={"manage-emp-select-country"}
                id="manage-emp-select-country"
                onChange={e => getEmployee(e)}
                showArrow
                showSearch
                optionFilterProp={CHILDREN}
              >
                {employeeList?.map(emp => {
                  return (
                    <Option key={emp?.employeeId} value={emp?.employeeId}>
                      {emp?.employeeName}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Form>
        </Col>
        {/* <RestrictedContainer assetId={NAVIGATION_ASSETS.ManageEmployee} assetRight={NAVIGATION_ASSETS_RIGHTS.CREATE}>
          <Col className="mb-2 text-xl-right text-md-right " xs={24} md={12} lg={6} xl={6}>
            <Button type="primary" onClick={showModal} id="manage-emp-addbtn">
              <EditOutlined className="mr-2" id="manage-emp-addbtn-icon" />
              {getMessage("component.manageemployees.substatus.updatestatus")}
            </Button>
          </Col>
        </RestrictedContainer> */}
      </Row>
      <Divider />
      {editEmployee ? (
        <Form
          {...formItemLayout}
          layout={"horizontal"}
          form={form}
          labelAlign="left"
          onFinish={HandleSubmit}
          initialValues={editEmployee ? initialData : { role: "Employee" }}
          requiredMark=""
        >
          <Row gutter={24}>
            <Col className="mb-1" xs={24} md={12} lg={8}>
              <Form.Item
                label={<div className="text-wrap">{getMessage(EMP_FORM_FIELDS.EMPLOYEE_ID.label)}</div>}
                name={EMP_FORM_FIELDS.EMPLOYEE_ID.name}
                rules={[
                  {
                    required: isEditMode() ? false : true,
                    message: getMessage(COMMON_MESSAGES.REQIRED_PREFIX, EMP_FORM_FIELDS.EMPLOYEE_ID.label),
                  },
                ]}
              >
                <span className="profile-text-color">{editEmployee?.employeeId}</span>
              </Form.Item>
            </Col>
            <Col className="mb-1" xs={24} md={12} lg={8}>
              <Form.Item label={getMessage(EMP_FORM_FIELDS.HRMS_ID.label)} name={EMP_FORM_FIELDS.HRMS_ID.name}>
                <span className="profile-text-color">{editEmployee?.hrmsId}</span>
              </Form.Item>
            </Col>
            <Col className="mb-1" xs={24} md={12} lg={8}>
              <Form.Item
                label={<div className="text-wrap">{getMessage(EMP_FORM_FIELDS.EMPLOYEE_NAME.label)}</div>}
                name={EMP_FORM_FIELDS.EMPLOYEE_NAME.name}
              >
                <span className="profile-text-color">{editEmployee?.employeeName}</span>
              </Form.Item>
            </Col>

            <Col className="mb-1" xs={24} md={12} lg={8}>
              <Form.Item label={getMessage(EMP_FORM_FIELDS.EMAIL.label)} name={EMP_FORM_FIELDS.EMAIL.name}>
                <span className="profile-text-color">{editEmployee?.emailId}</span>
              </Form.Item>
            </Col>
            <Col className="mb-1" xs={24} md={12} lg={8}>
              <Form.Item
                label={<div className="text-wrap">{getMessage(EMP_FORM_FIELDS.DOJ.label)}</div>}
                name={EMP_FORM_FIELDS.DOJ.name}
              >
                <span className="profile-text-color">
                  {editEmployee?.dateOfJoining && moment(editEmployee?.dateOfJoining).format(DATE_FORMAT)}
                </span>
              </Form.Item>
            </Col>

            <Col className="mb-1" xs={24} md={12} lg={8}>
              <Form.Item
                name={EMP_FORM_FIELDS.EMP_STATUS.name}
                label={getMessage(EMP_FORM_FIELDS.EMP_STATUS.label)}
                rules={[{ required: isEditMode() ? false : true }]}
                initialValue={STATUSES.ACTIVE_STATUS.value}
              >
                <Select
                  disabled={!isEditMode()}
                  placeholder={getMessage(EMP_FORM_FIELDS.EMP_STATUS.label)}
                  allowClear
                  onChange={e => setEmpStatus(e)}
                  id="emp-status"
                >
                  {Object.values(STATUSES)
                    .filter(x => x.value !== STATUSES.ALL_STATUS.value)
                    .map(e => {
                      return (
                        <Option key={e.value} value={e.value}>
                          {getMessage(e.label)}
                        </Option>
                      );
                    })}
                </Select>
              </Form.Item>
            </Col>
            {empStatus === STATUSES.INACTIVE_STATUS.value ? (
              <>
                <Col className="mb-1" xs={24} md={12} lg={8}>
                  <Form.Item
                    label={<div className="text-wrap">{getMessage(EMP_FORM_FIELDS.EXIT_DATE.label)}</div>}
                    name={EMP_FORM_FIELDS.EXIT_DATE.name}
                    rules={[
                      {
                        required: true,
                        message: getMessage(COMMON_MESSAGES.REQIRED_PREFIX, EMP_FORM_FIELDS.EXIT_DATE.label),
                      },
                    ]}
                  >
                    <DatePicker format={DATE_FORMAT} disabledDate={disabledDate} className="w-100" />
                  </Form.Item>
                </Col>

                <Col className="mb-1" xs={24} md={12} lg={8}>
                  <Form.Item
                    label={getMessage(EMP_FORM_FIELDS.EMPLOYEE_SUB_STATUS.label)}
                    name={EMP_FORM_FIELDS.EMP_SUB_STATUS.name}
                  >
                    {isEmpSubStatusUpDated() ? (
                      <span className="profile-text-color">{editEmployee?.empStatus || editEmployee?.subStatus}</span>
                    ) : (
                      <Select
                        placeholder={getMessage(EMP_FORM_FIELDS.EMPLOYEE_SUB_STATUS.label)}
                        allowClear
                        onChange={status => setEmployeSubStatus(status)}
                        // disabled={isEmpSubStatusUpDated()}
                        value={editEmployee?.subStatus}
                      >
                        {employeeSubStatusList?.map(status => {
                          return (
                            <Option key={status} value={status}>
                              {status}
                            </Option>
                          );
                        })}
                      </Select>
                    )}
                  </Form.Item>
                </Col>
                {employeeSubStatus && (
                  <Col className="mb-1" xs={24} md={12} lg={8}>
                    <Form.Item
                      label={
                        <div className="text-wrap">{`${employeeSubStatus} ${getMessage(
                          EMP_FORM_FIELDS.SUB_STATUS_START_DATE.label,
                        )}`}</div>
                      }
                      name={EMP_FORM_FIELDS.SUB_STATUS_START_DATE.name}
                      // rules={[
                      //   {
                      //     required: true,
                      //     message: getMessage(
                      //       COMMON_MESSAGES.REQIRED_PREFIX,
                      //       EMP_FORM_FIELDS.SUB_STATUS_START_DATE.label,
                      //     ),
                      //   },
                      // ]}
                    >
                      {isEmpSubStatusUpDated() ? (
                        <span className="profile-text-color">
                          {editEmployee?.empSubStatusStartDate
                            ? moment(editEmployee?.empSubStatusStartDate).format(DATE_FORMAT)
                            : editEmployee?.fromDate
                            ? moment(editEmployee?.fromDate).format(DATE_FORMAT)
                            : null}
                        </span>
                      ) : (
                        <DatePicker
                          onSelect={value => setEmpSubStatusStartDate(value)}
                          format={DATE_FORMAT}
                          disabledDate={disabledBeforeDate}
                          id="emp-date-of-joining"
                          className="w-100"
                          //      disabled={isEmpSubStatusUpDated()}
                        />
                      )}
                    </Form.Item>
                  </Col>
                )}
                {employeeSubStatus && (
                  <Col className="mb-1" xs={24} md={12} lg={8}>
                    <Form.Item
                      label={
                        <div className="text-wrap">{`${employeeSubStatus} ${getMessage(
                          EMP_FORM_FIELDS.SUB_STATUS_END_DATE.label,
                        )}`}</div>
                      }
                      name={EMP_FORM_FIELDS.SUB_STATUS_END_DATE.name}
                      // rules={[
                      //   {
                      //     required: true,
                      //     message: getMessage(
                      //       COMMON_MESSAGES.REQIRED_PREFIX,
                      //       EMP_FORM_FIELDS.SUB_STATUS_END_DATE.label,
                      //     ),
                      //   },
                      // ]}
                    >
                      {isEmpSubStatusUpDated() ? (
                        <span className="profile-text-color">
                          {editEmployee?.empSubStatusEndDate
                            ? moment(editEmployee?.empSubStatusEndDate).format(DATE_FORMAT)
                            : editEmployee?.toDate
                            ? moment(editEmployee?.toDate).format(DATE_FORMAT)
                            : null}
                        </span>
                      ) : (
                        <DatePicker
                          format={DATE_FORMAT}
                          //  disabled={isEmpSubStatusUpDated()}
                          className="w-100"
                          disabledDate={disableBeforeDate}
                        />
                      )}
                    </Form.Item>
                    <Col>
                      {errorMessage && (
                        <div className="text-left my-3" style={{ color: "red" }}>
                          * {errorMessage}
                        </div>
                      )}
                    </Col>
                  </Col>
                )}
              </>
            ) : (
              ""
            )}
          </Row>
          <Col>
            <Form.Item>
              <Button type="primary" htmlType="submit">
                Update
              </Button>
            </Form.Item>
          </Col>
        </Form>
      ) : (
        <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} />
      )}
    </>
  );
};

export default EmployeeStatus;
